
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css';
import axios from 'axios';
export default {
name:'create',
components:{
Group,XTextarea,
mavonEditor,
XButton,
Toast
},
data(){
return {
isShowToast:false,//
toastText:'',
title:'',
content:"",
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 

mark: true, // 


quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: false, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
}
}
},
methods:{

addtopic(){
var that=this;
this.$axios.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'
}).then(function(res){

that.$router.replace('/topiclist/dev');
}).catch(function(error){

that.isShowToast=true;
that.toastText='';
})
},

uploadImage(pos,file){
var that=this;



var params = new FormData();
params.append('smfile', file);
axios.post('https://sm.ms/api/v2/upload',params,{

headers:{'Content-type':"multipart/params"}
}).then(function(res){





})
}
}
}



