
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name:"edit",
props:['id'],
components:{
Group,XTextarea,
mavonEditor,
XButton,
Toast
},
data(){
return {
topic:{
title:'',
content:''
},
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 

mark: true, // 


quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: false, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
}
}
},
methods:{

edittopic:function(){
var that=this;
this.$axios.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
title:this.topic.title,
tab:'dev',
content:this.topic.content,
topic_id:this.topic.id
}).then(function(res){

that.$router.go(-1);

})
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$axios.get('/topic/'+to.params.id).then(function(res){

vm.topic=res.data.data;
})
})
},
beforeRouteUpdate:function(to,from,next){
var that=this;
this.$axios.get('/topic/'+to.params.id).then(function(res){

that.topic=res.data.data;
})
next();
}
}



